import CoinGecko from 'coingecko-api';

const CoinGeckoClient = new CoinGecko()



const GET = {
    all_price_voluem : (data)=> CoinGeckoClient.simple.price(data)
    .then(response => response.code === 200 ? response : console.error(response)),
    all_history_data: (coin,data) => CoinGeckoClient.coins.fetchMarketChartRange(coin,data)
    .then(response => response.code === 200 ? response : console.error(response)),
    UTF : ()=>{
        const current_date = new Date().getTime().toFixed().split('')
      return +current_date.slice(0,current_date.length- 3).join('') 
    }
    // .then(response => setDataCoint(response.data))
}

export default GET