import { useEffect, useState } from "react";


const dataY = {
    year : 31536000,
    month : 2678400,
    day : 86400
}


let num = 0
const arrD = []

const BarRight = props => {
    const [data, setData]  = useState([]) 
    const [coin,setCoint] = useState(0)
//  const {data,setData}  =props
const {all_history_data, UTF,all_price_voluem} = props.GET
// useEffect(()=>{

// },[data])

const arr = props.allCriptsSymbol;


const infinityFun = ()=>{
    current_cripto(arr[num++],90)
    arr.length === num && (num = 0)
    setTimeout(()=>setCoint(coin+1),5000)
    // setTimeout(infinityFun,2000)
    // setTimeout(infinityFun,10000)
    
}

const current_cripto = (coin,day) => {
    const dat =  UTF()
  let  from = dat -  dataY.day*day
    all_history_data(coin, {
        from ,
        to : dat
    })
    .then(d=>getChangeHist(d,day,coin))
  
}
const maxF = d => {
let sum = 0;
d.forEach(n=>{
    if(sum < n[1]){
        sum = n[1]
    }
})
return sum
}
const minF = d => {
    let sum = d[0][1];
    d.forEach(n=>{
        if(sum > n[1]){
            sum = n[1]
        }
    })
    return sum
}
 
const getChangeHist = (arr,day,coin) => {
let chast = 0
if(day === 1) {
    chast = 1 * 24 *12
} else if ( day <= 90) {
    chast = 24 * day
} else {
    chast = day
}

let sum = arr.data.prices.reduce((sum,d)=>d[1]+sum,0)

let max = maxF(arr.data.prices)
let min = minF(arr.data.prices)

const modlSum = sum / chast
let sumCheck = (modlSum - min) / 2 + min

all_price_voluem({
    ids :[coin],
    vs_currencies : ['usd'],
    include_24hr_vol:true
  })
  .then((respons)=>{
      const current_price = respons.data[coin].usd;

    if(sumCheck > current_price) {
        // alert(`
        // name : ${coin}
        // current_price : ${current_price}
        // min : ${min}
        // max : ${max}

        // `);
        const curD = {
            coin,min,max,current_price
        }
        // const d = JSON.parse(localStorage.getItem('coin'))
        // localStorage.setItem('coin', JSON.stringify([...d,curD]))
        // data.push([...data,curD])
        // console.log(data);
        arrD.push(curD)
        setData(arrD)
        console.log(arrD);
        
    }

  })
}
useEffect(()=>{

},[data])

    useEffect(()=>{
        
        infinityFun(coin)
    },[coin])

    return <div className="BarRight"> 
    <div className="currentBar">
       {
       data.map((elm,i)=> <CurrentD key={elm.coin+'d'+i} {...elm} />)
       }
    </div>
</div>
}

export default BarRight

const CurrentD = (elm)=>{
    console.log(elm);
    return <div className='CurrentD'>
   <p>name : {elm.coin}</p> 
   <p>current_price : {elm.current_price}</p> 
   <p>min : {elm.min}</p>
   <p>max : {elm.max}</p>
  
    </div>
}