
const Current_coin = props => {

    return <div className="Coin"> 
        <p className="Coin_name">
            {props.name}
        </p>
        <span className="Coin_usd">
            usd - {props.usd}
        </span>
        <span className="Coin_vol">
            vol/24 - {props.vol}
        </span>

    </div>
}

export default Current_coin
