import { useEffect, useState } from "react";
import imgFavorit from '../../../img/star_48px.png'
import Current_coin from "./Current_coin";


const dataGET = (allCriptsSymbol) => ({
    // ids :['don-key','pancakeswap-token'],
    ids :allCriptsSymbol,
    vs_currencies : ['usd'],
    include_24hr_vol:true
  })

const BarLeft = props => {


const [arr_Coin, setArrCoin] = useState([])
useEffect(()=>{
    props.GET.all_price_voluem(dataGET(props.allCriptsSymbol))

    .then(response=>changeInArr(response.data))
    .then(setArrCoin)
},[])



const changeInArr = allCoin => {
    if(!allCoin) return
    const Arr = []
    for(const coin in allCoin) {
        Arr.push({
            name : coin,
            usd : allCoin[coin]['usd'],
            vol : allCoin[coin]['usd_24h_vol']
        })
    }
    return Arr
}
    return <div className="BarLeft"> 
        <div className="currentBar">
            <div className="favorit">
                <img src={imgFavorit} />
                </div>
                        {
                             arr_Coin.map(d=> <Current_coin key={d.name} {...d}/>)
                        
                        }

        </div>
    </div>
}

export default BarLeft
