import { useEffect, useState } from "react";

const Header = props => {


const [option, setOption] = useState(null)
useEffect(()=>{

    setOption( props.allCriptsSymbol.map(name=><option key={name}>{name}</option>) )
},[])
    return <header>
        <select>
             {option ? option : ''}
        </select>
        <div>
          <input />
          <button>Find</button>
        </div>


    </header>
}

export default Header